import cn from 'classnames';
import { Todo } from '../../types/Todo';

type Props = {
  todo: Todo,
  showTodo: (todo: Todo) => void,
  selectedTodo: Todo | null,
};

export const TodoItem: React.FC<Props> = ({ todo, showTodo, selectedTodo }) => {
  const { id, completed, title } = todo;
  const isTodoSelected = selectedTodo && selectedTodo.id === id;

  return (
    <tr
      data-cy="todo"
      className={cn({ 'has-background-info-light': isTodoSelected })}
    >
      <td className="is-vcentered">{id}</td>
      <td className="is-vcentered">
        {completed && (
          <span className="icon" data-cy="iconCompleted">
            <i className="fas fa-check" />
          </span>
        )}
      </td>
      <td className="is-vcentered is-expanded">
        <p
          className={cn({
            'has-text-danger': !completed, 'has-text-success': completed,
          })}
        >
          {title}
        </p>
      </td>
      <td className="has-text-right is-vcentered">
        <button
          data-cy="selectButton"
          className="button"
          type="button"
          onClick={() => {
            showTodo(todo);
          }}
        >
          <span className="icon">
            <i className={cn('far', {
              'fa-eye-slash': isTodoSelected,
              'fa-eye': !isTodoSelected,
            })}
            />
          </span>
        </button>
      </td>
    </tr>
  );
};
